import React, { Fragment } from 'react';

import { Helmet } from 'react-helmet';

export default () => (
  <Fragment>
    <div>Main page!</div>
    <Helmet>
      <title>Main page title</title>
      <meta
        property="og:url"
        content="https://amortka.github.io/react-seo-tests/"
      />
      <meta property="og:type" content="article" />
      <meta property="og:title" content="MAIN page title" />
      <meta
        property="og:description"
        content="This is description of MAIN page"
      />
      <meta
        property="og:image"
        content="https://farm4.staticflickr.com/3857/33369071802_610da50ee8_b.jpg"
      />
    </Helmet>
  </Fragment>
);



