import React, { Fragment } from 'react';

import { Helmet } from 'react-helmet';

export default () => (
  <Fragment>
    <div>Main page!</div>
    <Helmet>
      <title>About page title</title>
      <meta
        property="og:url"
        content="https://amortka.github.io/react-seo-tests/"
      />
      <meta property="og:type" content="article" />
      <meta property="og:title" content="ABOUT page title" />
      <meta
        property="og:description"
        content="This is description of ABOUT page"
      />
      <meta
        property="og:image"
        content="https://farm3.staticflickr.com/2934/14496861929_ec77292c7b_k.jpg"
      />
    </Helmet>
  </Fragment>
);



